<template>
  <div class="row">
    <span class="center col number s1">{{ number }}</span>
    <div class="col subject s9">
      <span class="name">{{ name }}</span>
      <span class="info">{{ startTime }} - {{ endTime }}, {{ room }} </span>
      <span class="homework" @click="showHomework">{{ homework }}</span>
    </div>
    <div class="col marks s2">
      <span
        :key="assignment.id"
        v-for="assignment in assignments"
        @click="$parent.$parent.showMark(assignment)"
        :class="{ good: assignment.mark == 5, bad: assignment.mark <= 3 }"
        >{{ assignment.mark }}</span
      >
    </div>
  </div>
</template>

<script>
export default {
  props: [
    "name",
    "room",
    "number",
    "endTime",
    "homework",
    "startTime",
    "assignments"
  ],
  methods: {
    showHomework(e) {
      e.target.classList.toggle("show");
    }
  }
};
</script>
